import { Component } from 'react'
import Header from './Header'

export default class Layout extends Component {
  render ({ children }) {
    return (
      <div>
        <Header />
        {children}
        <style jsx global>{`
          body {
            margin: 0;
            font-family: system-ui;
            background: white;
          }
        `}</style>
      </div>
    )
  }
}



